import deepClone from "lodash.clonedeep";
import moment from "moment";

export default class Coverpage {
  constructor() {
    this.content = [];
  }
  Coverpage(areaData, contentList, pos) {
    this.content = contentList;
    // this.content.push({
    //   width: 100,
    //   height: 50,
    //   margin: -10
    // })
    this.content.push({
      // columns: [
      // {
      stack: [
        {
          width: "70%",
          style: "tableExample",
          color: "#444",
          table: {
            widths: ["auto", "*", "auto", "auto"],
            // headerRows: 2,

            // keepWithHeaderRows: 1,
            body: [
              [
                {
                  image:
                    "data:image/png;base64,",
                  rowSpan: 3,
                  width: 100,
                  height: 50,
                  alignment: "center",
                },
                {
                  text: [
                    { text: "HACCHU Answer Sheet\n", style: "tableHeader" },
                    { text: "PRE-ESTIMATION / PRE-DATA", style: "tableHeader" },
                  ],
                  rowSpan: 3,
                  margin: [0, 10, 0, 0],
                  alignment: "center",
                },

                {
                  text: "DATE",
                  colSpan: 2,
                  style: "thead",
                  alignment: "center",
                },
                {},
              ],
              [
                {},

                {},

                {
                  text: "Est._Date",
                  style: "tableHeader",
                  alignment: "center",
                },
                {
                  text: "Batch_Rcvd.",
                  style: "tableHeader",
                  alignment: "center",
                },
              ],
              [
                {},

                {},

                {
                  text: `${moment().format("YYYY/MM/DD")}`,
                  style: "tableHeader",
                  alignment: "center",
                },
                {
                  text: `${moment().format("YYYY/MM/DD")}`,
                  style: "tableHeader",
                  alignment: "center",
                },
              ],
            ],
          },
        },
      ],
    });
    this.content[0].stack.push({
      width: "70%",
      style: "tableExample",
      color: "#444",
      margin: [0, -15, 0, 0],
      table: {
        widths: ["*", "*", "*", "*", "*", "*"],
        // headerRows: 2,

        // keepWithHeaderRows: 1,
        body: [
          [
            {
              text: "Plan Code",
              style: "tableHeader",
              alignment: "center",
            },

            {
              text: "Kanabakari",
              style: "tableHeader",
              alignment: "center",
            },

            {
              text: "Data User",
              style: "tableHeader",
              alignment: "center",
            },

            {
              text: "House Type",
              style: "tableHeader",
              alignment: "center",
            },

            {
              text: "Estimator",
              style: "tableHeader",
              alignment: "center",
            },

            {
              text: "Date checked",
              style: "tableHeader",
              alignment: "center",
            },
          ],
          [
            {
              text: `${areaData.planInfo.plan_code}`,
              style: "tbody1",
              alignment: "center",
            },

            {
              text: `${areaData.planInfo.kanabakari}`,
              style: "tbody1",
              alignment: "center",
            },

            {
              text: `${areaData.planInfo.data_user}`,
              style: "tbody1",
              alignment: "center",
            },

            {
              text: `${areaData.planInfo.houseType}`,
              style: "tbody1",
              alignment: "center",
            },

            {
              text: `${areaData.planInfo.employee_name}`,
              style: "tbody1",
              alignment: "center",
            },

            {
              text: `${areaData.planInfo.date}`,
              style: "tbody1",
              alignment: "center",
            },
          ],
          [
            {
              text: "Plan No.",
              style: "tableHeader",
              alignment: "center",
            },

            {
              text: "Module No.",
              style: "tableHeader",
              alignment: "center",
            },

            {
              text: "Tile Type",
              style: "tableHeader",
              alignment: "center",
            },

            {
              text: "Fireproof Type",
              style: "tableHeader",
              alignment: "center",
            },

            {
              text: "F-Checker",
              style: "tableHeader",
              alignment: "center",
            },

            {
              text: "Roof Material",
              style: "tableHeader",
              alignment: "center",
            },
          ],
          [
            {
              text: `${areaData.planInfo.plan_no}`,
              style: "tbody1",
              alignment: "center",
            },

            {
              text: `.${areaData.planInfo.module_no}`,
              style: "tbody1",
              alignment: "center",
            },

            {
              text: `${areaData.planInfo.fireproof}`,
              style: "tbody1",
              alignment: "center",
            },

            {
              text: `${areaData.planInfo.employee_name}`,
              style: "tbody1",
              alignment: "center",
            },

            {
              text: `${areaData.planInfo.employee_name}`,
              style: "tbody1",
              alignment: "center",
            },

            {
              text: `${areaData.planInfo.roof_material}`,
              style: "tbody1",
              alignment: "center",
            },
          ],
        ],
      },
    });

    this.content[0].stack.push({
      width: "70%",
      style: "tableExample",
      color: "#444",
      margin: [0, 0, 0, 0],
      table: {
        widths: ["*", "*", "*", "*", "*", "*"],
        // headerRows: 2,

        // keepWithHeaderRows: 1,
        body: [
          [
            {
              text: "ID",
              style: "tableHeader",
              alignment: "center",
            },
            {
              text: "ID CSV",
              style: "tableHeader",
              alignment: "center",
            },

            {
              text: "Title",
              style: "tableHeader",
              alignment: "center",
            },

            {
              text: "Value",
              style: "tableHeader",
              alignment: "center",
            },

            {
              text: "Remarks",
              style: "tableHeader",
              alignment: "center",
            },

            {
              text: "Unit",
              style: "tableHeader",
              alignment: "center",
            },
          ],
        ],
      },
    });
    let items = deepClone(areaData.items);
    for (let i = 0; i < items.length; i++) {
      let trow = [];
      for (let key in items[i]) {
        if (key == "title") {
          items[i][key] = items[i][key].match(/\((.*)\)/).pop();
        }
        if (key == "value") {
          items[i][key] = Number(
            Math.round(items[i][key] + "e" + 3) + "e-" + 3
          );
        }

        trow.push({
          text: items[i][key],
          alignment: "center",
          style: "tbody1",
          margin: [0, 1],
        });
      }
      this.content[0].stack[2].table.body.push(trow);
    }
  }
}
